<template>
  <el-menu class="el-menu-demo" mode="horizontal" :router=true background-color="#add8e6"
    style="position:fixed;width:100%">
    <el-menu-item index="/">
      <img src="../../public/logo.jpg" class="logo" />
    </el-menu-item>
    <el-menu-item index="/search">Search</el-menu-item>
    <el-menu-item index="/registration">Test Registration</el-menu-item>
    <el-menu-item index="5">About Us</el-menu-item>
    <el-menu-item v-if="!isAuthenticated" index="/login">Login</el-menu-item>
    <el-menu-item v-if="isAuthenticated" index="/login" @click="logout">Logout</el-menu-item>
    <el-menu-item index="/register">Register</el-menu-item>
  </el-menu>
</template>

<script>
import store from '@/store'

export default {
  name: 'NavBar',
  methods: {
    logout() {
      store.dispatch('logout')
      try {
        this.$message({
          type: 'success',
          message: 'Logged out!',
          duration: 2000
        })
      } catch {
        this.$message({
          type: 'error',
          message: 'Something went wrong',
          duration: 2000
        })
      }
    },
  },
  computed: {
    isAdmin() {
      return store.getters.isAdmin
    },
    isAuthenticated() {
      return store.getters.isAuthenticated
    }
  }
}
</script>

<style scoped>
.logo {
  max-height: 40px;
  margin-top: 25%;
  /* height: 10%;
  width: 10%; */
}

.el-menu-demo {
  background-color: #add8e6;
}
</style>
