<template>
  <div class="common-layout">
    <el-container>
      <el-header v-if="showNavbar" class="header">
        <NavBar />
      </el-header>
      <el-container>
        <!-- <el-aside width="64px" class="sidebar"><SideBar /></el-aside> -->
        <el-container>
          <el-main :class="!showNavbar ? 'body-none' : 'body'">
            <router-view />
          </el-main>
          <el-footer class="footer" v-if="showNavbar">
            <FooterEl />
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import NavBar from './components/NavBar.vue'
// import SideBar from './components/SideBar.vue'
import FooterEl from './components/FooterEl.vue'

export default {
  name: 'App',
  components: {
    NavBar,
    // SideBar,
    FooterEl
  },
  computed: {
    showNavbar() {
      const currentRoute = this.$route.path;
      return currentRoute !== '/print';
    },
  },
}
</script>

<style scoped>
.header {
  background-color: #add8e6;
  padding: 0;
  position: fixed;
  width: 100%;
  z-index: 4;
}
.sidebar {
  position: fixed;
  margin-top: 4%;
  /* background-color:crimson;
  height: 99.9vh; */
}
.body {
  margin: 4%;
  margin-bottom: 0;
  width: 96%;
  min-height: 84.5vh;
}
.body-none {
  margin: 0;
}
.footer {
  background-color: lightblue;
  padding: 1.3%;
  position: relative;
  bottom: 0;
  width: 100%;
}
</style>
