import axios from "axios"
import config from '@/config'
import store from "@/store"

export const getTestByKey = async keyWord => {
  const headers = {
    'Content-Type': 'application/json',
    'Authorization': store.getters.token
  }
  const res = await axios.get(`${config.apiBaseUrl}/tests/search?keyWord=${keyWord}`, { headers })
  return res.data
}

export const getTestByDates = async (start, end) => {
  const headers = {
    'Content-Type': 'application/json',
    'Authorization': store.getters.token
  }
  const res = await axios.get(`${config.apiBaseUrl}/tests/dates?start=${start}&end=${end}`, { headers })
  return res.data
}
