<template>
  <el-tabs type="border-card">
    <el-tab-pane label="Search By Date">
      <p>Start Date: </p>
      <el-date-picker
        v-model="startDay"
        type="date"
        placeholder="Pick start day"
        size="large"
      />
      <br><br>
      <p>End Date: </p>
      <el-date-picker
        v-model="endDay"
        type="date"
        placeholder="Pick start day"
        size="large"
      />
      <br><br>
      <el-button
        color="#add8e6"
        size="large"
        :icon="searchIcon"
        @click="findByDates"
      >
        Search
      </el-button>
      <br><br>
    </el-tab-pane>
    <el-tab-pane label="Search By Keyword">
      <el-input
        v-model="keyWord"
        style="width: 40%;"
        size="large"
        placeholder="Search by PatientName, TotalCost, BalanceCost, Mobile, Email..."
        :suffix-icon="searchIcon"
      />
      <br><br>
      <el-button
        color="#add8e6"
        size="large"
        :icon="searchIcon"
        @click="findByKey"
      >
        Search
      </el-button>
      <br><br>
    </el-tab-pane>
  </el-tabs>
  <br>  
  <el-table
        v-loading="loading"
        element-loading-text="Getting Tests for you..."
        :data="fetchedData"
        border
        :show-summary="true"
        :summary-method="getSummaries"
      >
      <el-table-column
        prop="registrationDate"
        label="Date"
        width="150"
        :formatter="formatDate('registrationDate')"
      />
      <el-table-column prop="patientName" label="Name" width="250" />
      <el-table-column
        prop="dob"
        label="Date of Birth"
        width="150"
        :formatter="formatDate('dob')"
      />
      <el-table-column
        prop="age"
        label="Age"
        :formatter="ageFormat"
        width="150"
      />
      <el-table-column prop="gender" label="Sex" width="150" />
      <el-table-column
        prop="selectedTests"
        label="Tests"
        width="522"
        :formatter="joinArray"
      />
      <el-table-column prop="city" label="City" width="150" />
      <el-table-column prop="referredDoctor" label="Referred By" width="150" />
      <el-table-column prop="paymentMethod" label="Payment Method" width="150" />
      <el-table-column prop="balance" label="Balance" width="150" />
      <el-table-column
        fixed="right"
        prop="totalCost"
        label="Cost"
        width="150"
      />
    </el-table>
  
</template>

<script>

import { ref } from 'vue';
import { Search } from '@element-plus/icons-vue';
import { getTestByDates, getTestByKey } from '@/services/testServices';
import store from '@/store';

export default {
  name: 'SearchPage',
  data() {
    return {
      keyWord: '',
      startDay: new Date(),
      endDay: new Date(),
      tableData: [],
      loading: false,
    };
  },
  methods: {
    joinArray(row) {
      return row.selectedTests.join(', ');
    },
    formatDate(prop) {
      return (row) => row[prop].slice(0, 10).split('-').reverse().join('-');
    },
    setLoading() {
      this.loading = !this.loading;
    },
    ageFormat(row) {
      return `${row.age} ${row.ageUnit}`;
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];

      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = 'Total Cost';
          return;
        }

        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => Number.isNaN(value))) {
          sums[index] = `${values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!Number.isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0)}`;
        } else {
          sums[index] = null;
        }
        if (column.label === 'Age') {
          sums[index] = null;
        }
      });

      return sums;
    },
    async findByKey() {
      if (!store.getters.isAuthenticated) {
        this.$message({
          type: 'warning',
          message: 'Please login to search...',
        });
        return
      }
      if (this.keyWord !== '') {
        if (!store.getters.isAdmin) {
          this.$message({
            type: 'warning',
            message: 'Please login as Admin...',
          });
          return
        }
        this.setLoading();
        try {
          this.tableData = await getTestByKey(this.keyWord);
          if (this.tableData.length === 0) {
            this.$message({
              type: 'error',
              message: 'No data found :(',
              // message: err.respone.data.message
            });
          } else {
            this.$message({
              type: 'success',
              message: 'Match found :)',
              // message: err.respone.data.message
            });
          }
        } catch (err) {
          this.$message({
            type: 'error',
            message: err,
            // message: err.respone.data.message
          });
        } finally {
          this.setLoading();
        }
      } else {
        this.$message({
          type: 'error',
          message: 'Enter data to search...',
        });
      }
    },
    async findByDates() {
      if (!store.getters.isAuthenticated) {
        this.$message({
          type: 'warning',
          message: 'Please login to search...',
        });
        return
      }
      if (!store.getters.isAdmin) {
        this.$message({
          type: 'warning',
          message: 'Please login as Admin...',
        });
        return
      }
      this.setLoading();
      try {
        this.tableData = await getTestByDates(this.startDay, this.endDay);
        if (this.tableData.length === 0) {
          this.$message({
            type: 'error',
            message: 'No data found :(',
            // message: err.respone.data.message
          });
        } else {
          this.$message({
            type: 'success',
            message: 'Match found :)',
            // message: err.respone.data.message
          });
        }
      } catch (err) {
        this.$message({
          type: 'error',
          message: err,
          // message: err.respone.data.message
        });
      } finally {
        this.setLoading();
      }
    },
  },
  setup() {
    const keyword = ref('');
    const searchIcon = Search;

    return {
      keyword,
      searchIcon,
    };
  },
  computed: {
    fetchedData() {
      return this.tableData.data;
    },
  },
};
</script>

<style scoped>
p {
  display: inline;
}
</style>
